body {
  margin: 0;
  background-color: rgb(49, 59, 71);
  font-family: 'Ubuntu' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.justify-left {
  text-align: left;
}

.resume-header {
  padding: 20px 20px 20px 20px;
  background-color: #313c4e;
}

.header-element {
  padding-left: 20px;
}

.user-name {
  font-size: 3em;
  font-weight: 800;
}

.uniform-box {
  padding-top: 30px;
  padding-bottom: 30px;
}

.center {
  align-items: center;
}

.resume {
  max-width:980px;
  margin:0 auto;
  margin-bottom: 30px;
}

.display-picture {
  border: 4px solid rgb(68, 147, 153);
}

.user-position {
  font-size: 1.5em;
  color: rgb(68, 147, 153);
  font-style: italic;
}