.content-body {
    background-color: white;
    padding: var(--bs-gutter-x, 0.75rem);
}

.content-header {
    color: rgb(68, 147, 153);
    font-size: 1.5em;
    border-bottom: 2px solid rgb(68, 147, 153);
    text-align: left;
}

.content-header-text {
    padding: 0px 0px 0px 10px;
}

.align-start {
    text-align: left;
}

.align-end {
    text-align: right;
}

.heavy {
    font-weight: bolder;
}

.top-margins {
    margin-top: 10px;
}

.top-margins-2 {
    margin-top: 20px;
}

.one-three-text {
    font-size: 1.3em;
}

.score {
    color: rgb(68, 147, 153);
    font-size: 0.8em;
    font-style: italic;
}

.separator {
    border-bottom: 1px dashed rgb(68, 147, 153);
}

.explanation {
    font-size: 0.9em;
}

.sub-body {
    padding: var(--bs-gutter-x, 0.75rem);;
}